import Vue from "vue"
import VueRouter from "vue-router"
import DropSchedule from "../components/DropList/DropSchedule"
import FullList from "../components/DropList/FullList"
import Home from "../components/Home/Home"
import RegularArticle from "../components/Articles/RegularArticle"
import Comments from "../components/Comments"

Vue.use(VueRouter)

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home,
  },
  {
    path: "/articles/:id",
    name: "RegularArticle",
    component: RegularArticle,
  },
  {
    path: "/dropList",
    name: "DropSchedule",
    component: DropSchedule,
  },
  {
    path: "/dropList/:week",
    component: FullList,
  },
  {
    path: "/shopArticle/:id",
    name: "RegularArticle",
    component: RegularArticle,
  },
  {
    path: "/comment",
    component: Comments,
  },
]

const router = new VueRouter({
  routes,
})

export default router
