<template>
  <div id="app">
    <div class="nav">
      <div class="navContainer" ref="nav">
        <div class="navBar" v-if="mobileSize != true">
          <div></div>
          <div>
            <ul>
              <li
                v-for="(item, index) in mokeData"
                :key="item.title + '1'"
                :style="{
                  color: activeNavTag == index ? '#FF7200' : '#FFFFFF',
                }"
                @click="handleNavTag(index)"
              >
                {{ item.title }}
              </li>
            </ul>
            <i @click="controlSearchBar" class="el-icon-search"></i>
            <el-button type="primary" round>登录</el-button>
          </div>
        </div>
        <div class="navBar" v-else>
          <div></div>
          <div>
            <i @click="controlSearchBar" class="el-icon-search"></i>
            <i class="el-icon-s-fold" @click="handleDrawer"></i>
          </div>
        </div>
      </div>
    </div>

    <div class="searchBar" :class="{ searchBarOpen: searchBar == true }">
      <div class="navContainer searchBarContainer">
        <form>
          <input
            v-model="searchInput"
            type="text"
            placeholder="请输入搜索内容"
          />
          <i class="el-icon-search" />
          <span v-if="searchInput != ''" @click="clearSeachInput">清空</span>
          <i @click="controlSearchBar" class="el-icon-close" />
        </form>
      </div>
    </div>

    <router-view />

    <div class="navDrawer">
      <el-drawer title="异潮" :visible.sync="drawer" size="90%" direction="rtl">
        <div class="drawerMenu">
          <ul>
            <li v-for="(item, index) in mokeData" :key="index">
              <div @click="navRouteLink(item.link)">
                <span>{{ item.title }}</span>

                <i
                  v-if="item.cate"
                  :class="[
                    navBarExpand[index] == 1
                      ? 'el-icon-arrow-up'
                      : 'el-icon-arrow-down',
                  ]"
                  @click="expandDrawerMenu(index)"
                ></i>
              </div>
              <transition name="slide-fade">
                <div v-if="navBarExpand[index] == 1">
                  <span v-for="cate in item.cate" :key="cate.title">{{
                    cate.title
                  }}</span>
                </div>
              </transition>
            </li>
          </ul>
        </div>
        <div class="navAppContainer">
          <span>下载APP</span>
          <img
            src="https://cdn.flyscoot.com/prod/images/default-source/mobile/app-store/tw_appstore.gif?sfvrsn=5fc5db80_2"
          />
          <img
            src="https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSijJ5G7URCNjt81pky11OmqO1CISXUfZvFiScEx3Pki9VH3rkcZxhDD1Swmw-gCfIgfg&usqp=CAU"
          />
        </div>
      </el-drawer>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      activeNavTag: 0,
      mobileSize: true,
      drawer: false,
      searchBar: false,
      mokeData: [
        {
          title: "资讯",
          link: "",
          cate: [
            {
              title: "精选",
              link: "",
            },
            {
              title: "球鞋",
              link: "",
            },
            {
              title: "服饰",
              link: "",
            },
            {
              title: "玩具",
              link: "",
            },
            {
              title: "手表",
              link: "",
            },
          ],
        },
        {
          title: "潮流导购",
          link: "",
        },
        {
          title: "发售清单",
          link: "/droplist/",
        },
      ],

      searchInput: "",
      navBarExpand: [],
    }
  },
  created() {
    this.getMenuList()
    for (var item in this.mokeData) {
      if (this.mokeData[item].cate) {
        this.navBarExpand[item] = -1
      }
    }
    console.log(this.navBarExpand)
  },
  mounted() {
    if (this.$refs.nav.clientHeight < 80) {
      this.mobileSize = true
    } else {
      this.mobileSize = false
    }
    const this_ = this
    window.onresize = () => {
      return (() => {
        if (this_.$refs.nav.clientHeight < 80) {
          this_.mobileSize = true
        } else {
          this_.mobileSize = false
        }

        if (this_.$refs.nav.clientWidth > 570) {
          this_.drawer = false
        }
      })()
    }
  },
  methods: {
    getMenuList() {},
    handleDrawer() {
      this.drawer = true
    },
    controlSearchBar() {
      this.searchInput = ""
      if (this.searchBar == false) {
        this.searchBar = true
      } else {
        this.searchBar = false
      }
    },
    expandDrawerMenu(index) {
      this.navBarExpand[index] = -this.navBarExpand[index]
      console.log(this.navBarExpand)
      this.$forceUpdate()
    },
    clearSeachInput() {
      this.searchInput = ""
    },
    handleNavTag(index) {
      this.activeNavTag = index
      if (index == 0) {
        this.$router.push("/")
      } else if (index == 1) {
        this.$router.push("/shop")
      } else if (index == 2) {
        this.$router.push("/droplist")
      }
    },
    navRouteLink(link) {
      this.drawer = false
      this.$router.push(link)
    },
  },
}
</script>

