<template>
  <div class="container">
    <div class="title">
      <p>2021年4月9号 发售清单</p>
      <p>
        All droplists for Palace Spring 2021. Click the specific droplist for
        details.
      </p>
    </div>
    <div class="itemContainer">
      <div
        v-for="(item, index) in dropList"
        v-bind:key="item.id"
        class="dropItem"
      >
        <div class="itemTop">
          <img @click="itemDetail(index)" class="dropItemImg" :src="item.img" />
          <div class="itemColors">{{ item.color }}</div>
        </div>

        <div class="itemName">{{ item.title }}</div>
        <div class="itemVoteContainer">
          <div class="itemVote">
            <div @click="handleThumbsUp(item.id, index)">
              <img
                v-if="votedItems[item.id] == 'up'"
                src="../../assets/thumbs_up_red.png"
              />
              <img v-else src="../../assets/thumbs_up.png" />
            </div>
            <div @click="handleThumbsDown(item.id, index)">
              <img
                v-if="votedItems[item.id] == 'down'"
                src="../../assets/thumbs_down_red.png"
              />
              <img v-else src="../../assets/thumbs_down.png" />
            </div>
          </div>
          <div class="itemVoteNumber">
            <section v-if="item.upNumber != 0 || item.downNumber != 0">
              <div :style="{ flex: item.upNumber }">{{ item.upNumber }}</div>
              <div :style="{ flex: item.downNumber }">
                {{ item.downNumber }}
              </div>
            </section>
            <section v-else>
              <div style="flex: 50">{{ item.upNumber }}</div>
              <div style="flex: 50">{{ item.downNumber }}</div>
            </section>
          </div>
        </div>
      </div>
    </div>

    <div class="itemDetail">
      <el-dialog title="商品详情" :visible.sync="itemDetailVisible">
        <div class="detailContainer">
          <div class="detailTop">
            <div class="detailTopImg">
              <img :src="detailItem.img" />
            </div>
            <div class="detailTopDes">
              <div class="detailDes">
                <div>{{ detailItem.title }}</div>
                <div>发售季度: 2021 Spring</div>
                <div>发售时间: 未知</div>
                <div>
                  <a href="https://www.baidu.com/">
                    <span>
                      商品链接
                      <i class="el-icon-link" />
                    </span>
                  </a>
                </div>
              </div>
              <div class="detailVote">
                <div class="itemVote">
                  <div @click="handleThumbsUp(detailItem.id, detailItem.index)">
                    <img
                      v-if="votedItems[detailItem.id] == 'up'"
                      src="../../assets/thumbs_up_red.png"
                    />
                    <img v-else src="../../assets/thumbs_up.png" />
                  </div>
                  <div
                    @click="handleThumbsDown(detailItem.id, detailItem.index)"
                  >
                    <img
                      v-if="votedItems[detailItem.id] == 'down'"
                      src="../../assets/thumbs_down_red.png"
                    />
                    <img v-else src="../../assets/thumbs_down.png" />
                  </div>
                </div>
                <div class="itemVoteNumber">
                  <section
                    v-if="
                      detailItem.upNumber != 0 || detailItem.downNumber != 0
                    "
                  >
                    <div :style="{ flex: detailItem.upNumber }">
                      {{ detailItem.upNumber }}
                    </div>
                    <div :style="{ flex: detailItem.downNumber }">
                      {{ detailItem.downNumber }}
                    </div>
                  </section>
                  <section v-else>
                    <div style="flex: 50">{{ detailItem.upNumber }}</div>
                    <div style="flex: 50">{{ detailItem.downNumber }}</div>
                  </section>
                </div>
              </div>
            </div>
          </div>
          <div class="detailBottom">
            <div class="detailBottomTitle">可选颜色</div>
            <div class="detailBottomItemContainer">
              <div
                v-for="(itemColors, index) in detailItem.colorList"
                :key="itemColors.id"
                class="detailBottomItem"
                @click="handleVoteColor(detailItem.id, index, itemColors.title)"
              >
                <div>
                  <img :src="itemColors.img" />
                </div>
                <div>{{ itemColors.title }}</div>
                <div>
                  {{ itemColors.likePercent }}%
                  <img
                    v-if="votedColors[detailItem.id] == itemColors.title"
                    src="../../assets/red_heart.png"
                  />
                  <img
                    v-else-if="votedColors[detailItem.id] == null"
                    src="../../assets/heart.png"
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      itemDetailVisible: false,
      dropList: [
        {
          id: 1,
          title: "PAL IS ACE JACKET",
          img:
            "https://images.palaceskateboards.com/wp-content/uploads/2021/02/palace-ss-21-mask-0648-CT-1024x717.jpg",
          color: "3色可选",
          upNumber: 0,
          downNumber: 0,
          colorList: [
            {
              img:
                "https://images.palaceskateboards.com/wp-content/uploads/2021/02/palace-ss-21-mask-0648-CT-1024x717.jpg",
              title: "TEAL",
              like: 1,
              id: 11,
            },
            {
              img:
                "https://images.palaceskateboards.com/wp-content/uploads/2021/02/palace-ss21-jkts-pockets0770-CT-1024x717.jpg",
              title: "STONE",
              like: 2,
              id: 22,
            },
            {
              img:
                "https://images.palaceskateboards.com/wp-content/uploads/2021/02/palace-ss21-jkts-pockets0783-CT-1024x717.jpg",
              title: "BLACK",
              like: 3,
              id: 33,
            },
          ],
        },
        {
          id: 2,
          title: "BOARDS",
          img:
            "https://images.palaceskateboards.com/wp-content/uploads/2021/02/palace-ss21-boards-0916-1024x717.jpg",
          color: "3色可选",
          upNumber: 0,
          downNumber: 2,
        },
        {
          id: 3,
          title: "BASICALLY A CORD 6-PANEL",
          img:
            "https://images.palaceskateboards.com/wp-content/uploads/2021/01/Palace-cap-6-cord-bwn-0633-1024x717.jpg",
          color: "3色可选",
          upNumber: 50,
          downNumber: 50,
        },
        {
          id: 4,
          title: "SQUARE PATCH CREW",
          img:
            "https://images.palaceskateboards.com/wp-content/uploads/2021/01/palace-ss-21-crew-square-patch-black-0053-1024x717.jpg",
          color: "3色可选",
          upNumber: 1,
          downNumber: 0,
        },
      ],
      votedItems: {},
      votedColors: {},
      detailItem: {},
    }
  },
  created() {
    this.getDropList()
  },
  mounted() {
    console.log(this.votedItems)
  },
  methods: {
    getDropList() {
      this.dropList = this.dropList
      if (localStorage.getItem("vote_color")) {
        this.votedColors = JSON.parse(localStorage.getItem("vote_color"))
      }
      if (localStorage.getItem("vote_item")) {
        this.votedItems = JSON.parse(localStorage.getItem("vote_item"))
      }
    },
    itemDetail(index) {
      //在detailItem中存入点击的item信息
      this.detailItem = this.dropList[index]
      //添加index属性，对应循环的顺序
      this.detailItem["index"] = index
      //初始化全部颜色总点击值
      var totolColorLikes = 0
      //循环具体商品的每一个颜色
      for (var index in this.detailItem.colorList) {
        //求和
        totolColorLikes =
          totolColorLikes + this.detailItem.colorList[index].like
      }
      console.log(totolColorLikes)
      //判断颜色是否有点击值
      if (totolColorLikes != 0) {
        //循环每一个颜色
        for (var index in this.detailItem.colorList) {
          //添加每一个颜色对应的比例
          this.detailItem.colorList[index]["likePercent"] = Math.floor(
            (this.detailItem.colorList[index].like * 100) / totolColorLikes
          )
        }
      }
      //需要一个else处理总点击数量为0的情况
      else {
      }
      this.itemDetailVisible = true
      console.log(this.detailItem)
    },
    handleThumbsUp(itemId, index) {
      //检查localStorage是否有值
      if (localStorage.getItem("vote_item")) {
        //获取localStorage的voteItem值
        var votedItems = localStorage.getItem("vote_item")
        console.log(votedItems)
        //检查是否已经存在点赞纪录，有点赞纪录返回
        if (votedItems.includes(itemId) == false) {
          //添加对应item的id值和加赞
          var jsonVotedItems = JSON.parse(votedItems)
          jsonVotedItems[itemId] = "up"
          console.log(jsonVotedItems)
          localStorage.setItem("vote_item", JSON.stringify(jsonVotedItems))
          //更新votedItems的值
          this.votedItems = JSON.parse(localStorage.getItem("vote_item"))
          this.dropList[index].upNumber++
        }
        //没有值初始化localStorage
      } else {
        var addVotedItem = `{"${itemId}": "up"}`
        localStorage.setItem("vote_item", addVotedItem)
        //更新votedItems的值
        this.votedItems = JSON.parse(localStorage.getItem("vote_item"))
        //更新前端页面对应的item的点赞值
        this.dropList[index].upNumber++
      }
    },
    handleThumbsDown(itemId, index) {
      if (localStorage.getItem("vote_item")) {
        var votedItems = localStorage.getItem("vote_item")
        console.log(votedItems)
        if (votedItems.includes(itemId) == false) {
          var jsonVotedItems = JSON.parse(votedItems)
          jsonVotedItems[itemId] = "down"
          console.log(jsonVotedItems)
          localStorage.setItem("vote_item", JSON.stringify(jsonVotedItems))
          this.votedItems = JSON.parse(localStorage.getItem("vote_item"))
          this.dropList[index].downNumber++
        }
      } else {
        var addVotedItem = `{"${itemId}": "down"}`
        localStorage.setItem("vote_item", addVotedItem)
        this.votedItems = JSON.parse(localStorage.getItem("vote_item"))
        this.dropList[index].downNumber++
      }
    },
    handleVoteColor(itemId, index, colorTitle) {
      //检查localStorage是否有值

      if (localStorage.getItem("vote_color")) {
        //获取localStorage的voteItem值
        var votedColors = localStorage.getItem("vote_color")
        console.log(votedColors)
        //检查是否已经存在点赞纪录，有点赞纪录返回
        if (votedColors.includes(itemId) == false) {
          //添加对应color的id值和赞
          var jsonVotedColors = JSON.parse(votedColors)
          jsonVotedColors[colorId] = 1
          console.log(jsonVotedColors)
          localStorage.setItem("vote_color", JSON.stringify(jsonVotedColors))
          //更新votedColors的值
          this.votedColors = JSON.parse(localStorage.getItem("vote_color"))
          console.log(this.votedColors)
          //添加投票后的颜色属性
          this.detailItem.colorList[index]["votedColor"] = colorTitle
          this.detailItem.colorList[index].like++
        }
        //没有值初始化localStorage
      } else {
        var addVotedColor = `{"${itemId}": "${colorTitle}"}`
        localStorage.setItem("vote_color", addVotedColor)
        //更新votedColors的值
        this.votedColors = JSON.parse(localStorage.getItem("vote_color"))
        //添加投票后的颜色属性
        this.detailItem.colorList[index]["votedColor"] = colorTitle
        //更新前端页面对应的color的点赞值
        this.detailItem.colorList[index].like++
      }
    },
  },
}
</script>

<style lang="less">
.itemDetail .el-dialog__title {
  font-weight: bold;
  font-size: 22px;
}
.itemDetail .el-dialog__headerbtn {
  font-size: 20px;
  .el-dialog__close {
    font-weight: bold;
  }
}
.itemDetail .el-dialog__header {
  border-bottom: 1px solid #e9ecef;
  padding: 20px;
}
.itemDetail .el-dialog__body {
  padding: 20px;
}
@media screen and (max-width: 767px) {
  .itemDetail .el-dialog {
    margin-top: 2vh !important;
    width: 92% !important ;
  }
}
@media screen and (min-width: 767px) and (max-width: 992px) {
  .itemDetail .el-dialog {
    margin-top: 10vh !important;
    width: 500px !important ;
  }
}
@media screen and (min-width: 992px) {
  .itemDetail .el-dialog {
    margin-top: 10vh !important;
    width: 800px !important ;
  }
}
</style>

