}
@media screen and (min-width: 1200px) {
  .container {
    max-width: 1140px;
  }
}
.title {
  text-align: center;
  margin-top: 20px;
  margin-bottom: 20px;
  p:nth-of-type(1) {
    font-weight: bold;
    font-size: 28px;
    margin-bottom: 10px;
  }
}
.dropItem {
  margin-bottom: 20px;
  max-height: 350px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: relative;
  box-shadow: 0 1px 5px gainsboro;
}
.itemTop {
  width: 100%;
  height: 0;
  padding-bottom: 100%;
  background-color: white;
  border-radius: 2px;
  overflow: hidden;
  position: relative;
}
.itemTop::after {
  content: "";
  display: block;
  margin-top: 100%;
}
